    right: 2px;
    width: 24px;
    height: 14px;
    transition-duration: 1s;
    cursor: pointer;
    border: none;
    padding: 24px;
    background: transparent;
    z-index: 20;
  }
}
.menu-toggle.focus-visible:focus {
  outline: 1px solid #00c8ff;
