.menu-toggle__line {
  height: 2px;
  width: 24px;
  background: #303030;
  position: absolute;
  left: 11px;
  transition-duration: 0.25s;
  transition-delay: 0.25s;
}
.menu-toggle__line::before {
  content: "";
  left: 0;
  position: absolute;
  top: -6px;
  height: 2px;
  width: 24px;
  background: #303030;
  transition: transform 0.25s, top 0.25s 0.25s;
}
.menu-toggle__line::after {
  content: "";
  left: 0;
  position: absolute;
  top: 6px;
  height: 2px;
  width: 24px;
  background: #303030;
  transition: transform 0.25s, top 0.25s 0.25s;
}

.wrapper--open-menu .menu-toggle__line {
  transition-duration: 0.1s;
  transition-delay: 0.1s;
  background: transparent;
}

.wrapper--open-menu .menu-toggle .menu-toggle__line::before {
  transition: top 0.25s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(-225deg);
  background: #303030;
  width: 25px;
}

.wrapper--open-menu .menu-toggle .menu-toggle__line::after {
  transition: top 0.4s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(225deg);
  background: #303030;
  width: 25px;
}

